.header {
  position: absolute;
  top: 35px;
  left: 40px;
  right: 40px;
  z-index: 2;
}
@media (max-width: 400px) {
.header {
      top: 30px;
      left: 20px;
      right: 20px;
}
}
.header header.pink .back-link:after {
    background-image: url(/static/img/menu-animation/pink.png);
}
.header header.blue .back-link:after {
    background-image: url(/static/img/menu-animation/blue.png);
}
.header header.green .back-link:after {
    background-image: url(/static/img/menu-animation/green.png);
}
.header header.yellow .back-link:after {
    background-image: url(/static/img/menu-animation/yellow.png);
}
.header header.purple .back-link:after {
    background-image: url(/static/img/menu-animation/purple.png);
}
.header header a {
    display: inline-block;
    text-decoration: none;
    letter-spacing: 1.7px;
    line-height: 16px;
    text-transform: uppercase;
    color: white;
}
.header header .logo img, .header header .logo h1 {
    display: inline-block;
    vertical-align: middle;
}
.header header .logo img {
    margin-right: 15px;
    width: calc(50px / 2);
    height: auto;
}
.header header .logo h1 {
    font-weight: normal;
    margin-top: 2px;
    font-size: 12px;
    letter-spacing: 1.7px;
    line-height: 16px;
    text-transform: uppercase;
}
@media (max-width: 400px) {
.header header .logo h1 {
        display: none;
}
}
.header header .more-back-links {
    float: right;
    font-weight: 600;
    padding-top: 5px;
}
.header header .more-back-links .back-link {
      position: relative;
      font-weight: 800;
      font-size: 11px;
      line-height: 14px;
      color: #3C3C3C;
      text-align: center;
      transition: color ease .2s;
}
.header header .more-back-links .back-link:hover:after {
        opacity: 1;
        animation: bounce 1.2s steps(39) reverse;
}
.header header .more-back-links .back-link.about {
        color: white;
        transition: transform .3s;
}
.header header .more-back-links .back-link.about:after {
          background-image: url(/static/img/menu-animation/red.png);
}
.header header .more-back-links .back-link:after {
        position: absolute;
        content: '';
        right: 0px;
        top: auto;
        left: auto;
        bottom: -12px;
        width: 29px;
        height: 102px;
        background-color: transparent !important;
        background-position: 0 0;
        opacity: 0;
        transition: opacity .4s;
}
@keyframes bounce {
100% {
    background-position: -1131px 0;
}
}



