html {
  font-family: "simplon";
  overflow-x: hidden;
}
* {
  outline: none;
}
.wrapper {
  max-width: 1080px;
  margin: 0 auto;
  padding: 20px;
}
.project-bar {
  height: 7px;
  width: 150px;
  display: inline-block;
}
.page {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding-top: 70px;
  padding-bottom: 100px;
}
.page-project {
  padding-top: 100px;
  padding-bottom: 0px;
}
@media (max-width: 768px) {
.page-project {
      padding-top: 80px;
}
}
.small-text,
.text,
.text-ancors,
.slider-title,
.slider-subtitle,
.project-title,
.project-subtitle,
.project-part__title {
  font-family: "simplon";
}
.small-text {
  font-size: 12px;
  text-transform: uppercase;
}
.about-container .text a {
  color: white;
  text-decoration: none;
}
.text {
  font-size: 14px;
  color: #3C3C3C;
  line-height: 21px;
}
.text strong {
    font-weight: bolder;
}
.text a {
    /*text-decoration: none;*/
    color: #3C3C3C;
    font-weight: 800;
}
.text-ancors {
  font-size: 12px;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 1.51px;
  position: relative;
  text-decoration: none;
  display: inline-block;
  padding-bottom: 4px;
}
.text-ancors:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
}
.slider-title,
.page-about-title {
  font-size: 34px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 3.2px;
  margin-bottom: 5px;
}
@media (max-width: 768px) {
.slider-title,
    .page-about-title {
      font-size: 26px;
}
}
.slider-subtitle,
.page-about-subtitle {
  font-size: 12px;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 0.9px;
  color: white;
}
.slider-subtitle span.last .line,
  .page-about-subtitle span.last .line {
    display: none;
}
@media (max-width: 768px) {
.slider-subtitle,
    .page-about-subtitle {
      white-space: normal;
}
.slider-subtitle span,
      .page-about-subtitle span {
        display: inline-block;
        margin-right: 2px;
        margin-bottom: 2px;
}
}
.project-title {
  font-size: 64px;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 6px;
}
@media (max-width: 768px) {
.project-title {
      font-size: 42px;
      letter-spacing: 3px;
}
}
.project-subtitle {
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 0.86px;
}
.project-subtitle span.last .line {
    display: none;
}
.project-part__title {
  font-size: 36px;
  text-transform: uppercase;
  font-weight: bold;
}
.project-header .project-header__content {
  margin-top: 30px;
  margin-bottom: 20px;
  max-width: 720px;
}
.project-part {
  margin-top: 35px;
  padding-top: 35px;
}
@media (max-width: 768px) {
.project-part {
      margin-top: 15px;
}
}
.project-part__header {
  padding-bottom: 50px;
}
.project-part__header .project-part__title {
    margin-bottom: 20px;
}
.project-part__header .project-part__intro {
    max-width: 645px;
}
.project-part.moodboard {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding-bottom: 50px;
}
@media (max-width: 768px) {
.project-part.moodboard {
      -ms-flex-direction: column;
          flex-direction: column;
      -ms-flex-align: start;
          align-items: flex-start;
      margin-top: 0;
}
}
.project-part.moodboard .project-part__header {
    padding-right: 50px;
    padding-bottom: 0px;
}
@media (max-width: 768px) {
.project-part.moodboard .project-part__header {
        margin-bottom: 30px;
}
}
.project-part.moodboard .project-part__header .project-part__intro {
      max-width: 380px;
}
.project-part.moodboard .project-part__picture {
    max-width: 100%;
    margin: 0 auto;
    text-align: center;
}
.project-part.moodboard .project-part__picture img {
      max-width: 630px;
}
@media (max-width: 768px) {
.project-part.moodboard .project-part__picture img {
          width: 90%;
}
}
.project-part__bannerIMG {
  width: 100%;
}
.project-part__bannerIMG img {
    max-width: 100%;
}
.project-part__bannerBG {
  width: 100%;
}
.project-part__bannerBG .project-part__bannerBackground {
    height: 380px;
    background-size: cover;
    background-position: 50% 50%;
}
@media (max-width: 768px) {
.project-part__bannerBG .project-part__bannerBackground {
        height: 280px;
}
}
.project-part.decoration {
  padding-top: 30px;
}
@media (max-width: 1024px) {
.project-part.decoration {
      display: none;
}
}
.project-part.decoration[data-disposition="fullsize"] .decoration-container {
    /*margin: 0 auto;*/
}
.project-part.decoration[data-disposition="contain"] .decoration-container {
    margin: 0 auto;
    max-width: 1080px;
}
.project-part.decoration .decoration-container {
    position: relative;
    height: 300px;
    z-index: -1;
}
.project-part.decoration .decoration-container img {
      position: absolute;
}
.project-part.decoration .decoration-container img:first-child {
        left: 0;
}
.project-part.decoration .decoration-container img:nth-child(2) {
        right: 0px;
        top: -100%;
        max-width: 500px;
}
.project-part__decoration {
  position: absolute;
  right: 0;
  z-index: -1;
}
@media (max-width: 768px) {
.project-part__decoration {
      display: none;
}
}
.project-part__preparation {
  position: relative;
  text-align: center;
}
.project-part__preparation img {
    max-width: 80%;
}
@media (max-width: 1024px) {
.project-part__preparation img {
        width: 100%;
}
}
.project-part__vimeo {
  width: 100%;
  text-align: center;
}
.project-part__vimeo iframe {
    width: 865px;
    height: 540px;
    margin: 0 auto;
}
@media (max-width: 1024px) {
.project-part__vimeo iframe {
        width: 100%;
        height: 400px;
}
}
@media (max-width: 768px) {
.project-part__vimeo iframe {
        width: 100%;
        height: 300px;
}
}
.project-part__vimeo .poster {
    max-width: 80%;
    height: auto;
}
#app {
  font-family: "simplon";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}



